.tasksColumnsList {
  &__container {
    max-width: 1800px;
    height: 95%;
    display: flex;
    width: 100%;
  }
}

@media (max-width: 1400px) {
  .tasksColumnsList {
    &__container {
      overflow-x: scroll;
      overflow-y: hidden;

      &::-webkit-scrollbar {
        height: 0;
      }
      &::-webkit-scrollbar-track {
        background-color: #e5ccff;
        border-radius: 100px;
      }
    }
  }
}